'use client'

import { useState, FormEvent } from 'react'
import { useRouter } from 'next/navigation'
import { Textarea } from '@/components/ui/Textarea'
import { Select } from '@/components/ui/Select'
import { Button } from '@/components/ui/Button'
import { Card } from '@/components/ui/Card'

interface IdeaFormProps {
  projects: Array<{ id: string; name: string }>
}

export function IdeaForm({ projects }: IdeaFormProps) {
  const router = useRouter()
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [formData, setFormData] = useState({
    content: '',
    type: 'ad_concept',
    projectId: '',
  })

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault()
    setIsSubmitting(true)

    try {
      const payload = {
        content: formData.content,
        type: formData.type,
        projectId: formData.type === 'ad_concept' && formData.projectId ? formData.projectId : null,
      }

      const response = await fetch('/api/ideas', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      })

      if (!response.ok) throw new Error('Failed to save idea')

      // Reset form
      setFormData({ content: '', type: 'ad_concept', projectId: '' })
      router.refresh()
    } catch (error) {
      console.error('Error saving idea:', error)
      alert('Failed to save idea. Please try again.')
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <Card className="sticky top-8">
      <h2 className="text-lg font-semibold text-neutral-900 mb-4">
        Add New Idea
      </h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        <Textarea
          label="Idea"
          placeholder="Write your idea here..."
          required
          rows={4}
          value={formData.content}
          onChange={(e) => setFormData({ ...formData, content: e.target.value })}
        />

        <Select
          label="Type"
          value={formData.type}
          onChange={(e) => setFormData({ ...formData, type: e.target.value, projectId: '' })}
        >
          <option value="ad_concept">Ad Concept (project-specific)</option>
          <option value="format">Format (reusable)</option>
        </Select>

        {formData.type === 'ad_concept' && projects.length > 0 && (
          <Select
            label="Project (Optional)"
            value={formData.projectId}
            onChange={(e) => setFormData({ ...formData, projectId: e.target.value })}
          >
            <option value="">No specific project</option>
            {projects.map((project) => (
              <option key={project.id} value={project.id}>
                {project.name}
              </option>
            ))}
          </Select>
        )}

        <Button type="submit" isLoading={isSubmitting} className="w-full">
          Save Idea
        </Button>
      </form>
    </Card>
  )
}
